.txt-accueil {
  border: 1px solid $green;
  height: auto;
  border-radius: 20px;
  max-width: 1180px;
  margin: 18vh auto 0;
  p {
    opacity: 0;
    animation: fadeIn 0.3s ease-out forwards;
    @keyframes fadeIn {
      from {
        opacity: 0;
      }
      to {
        opacity: 1;
      }
    }
  }
  #accueil-content {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;

    .center-home {
      padding: 80px 0 50px;
    }
    .content-top {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 10px;
      padding-bottom: 50px;
      h3 {
        opacity: 0;
        animation: fadeIn 0.3s ease-out forwards;
        @keyframes fadeIn {
          from {
            opacity: 0;
          }
          to {
            opacity: 1;
          }
        }
        @media screen and (max-width: 820px) {
          font-size: 0.9rem;
        }
      }
      img {
        height: 18px;
        width: 18px;
        opacity: 0;
        animation: fadeIn 0.3s ease-out forwards;
        @keyframes fadeIn {
          from {
            opacity: 0;
          }
          to {
            opacity: 1;
          }
        }
      }
    }

    .content-bot {
      h2 {
        font-size: 3.5rem;
        font-weight: bold;
        line-height: 1.1;
        display: inline-block;
        width: fit-content;
        opacity: 0;
        animation: fadeIn 0.3s ease-out forwards;
        @keyframes fadeIn {
          from {
            opacity: 0;
          }
          to {
            opacity: 1;
          }
        }
        @media screen and (max-width: 820px) {
          font-size: 2rem;
        }
        @media screen and (max-width: 560px) {
          font-size: 1.5rem;
        }
        // @media screen and (max-width: 530px) {
        //   padding-bottom: 0;
        // }
      }
      h3 {
        // max-width: 650px;
        // margin: 0 auto;
        line-height: 1.2;
        margin: 50px 15px;
        opacity: 0;
        animation: fadeIn 0.3s ease-out forwards;
        @keyframes fadeIn {
          from {
            opacity: 0;
          }
          to {
            opacity: 1;
          }
        }
        @media screen and (max-width: 820px) {
          font-size: 1rem;
        }
        @media screen and (max-width: 560px) {
          font-size: 0.9rem;
        }
      }
      .surlignage {
        color: $green;
      }
      #underligne1 {
        border-bottom: 4px solid #092915;
        padding-bottom: 5px;
        @media screen and (max-width: 530px) {
          border-bottom: none;
        }
      }
    }
    .buttons-accueil {
      padding-bottom: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 50px;

      button {
        padding: 15px 35px;
        border-radius: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;
        font-weight: 600;
        border: none;
        cursor: pointer;
        font-size: 1rem;
        transition: 0.4s ease-in-out;
        opacity: 0;
        animation: fadeIn 0.3s ease-out forwards;
        @keyframes fadeIn {
          from {
            opacity: 0;
          }
          to {
            opacity: 1;
          }
        }
        @media screen and (max-width: 820px) {
          font-size: 0.8rem;
          padding: 10px 25px;
        }
        @media screen and (max-width: 620px) {
          font-size: 0.65rem;
          padding: 8px 21px;
        }
        &:first-child {
          background: $green;
          color: $black;
          &:hover {
            transform: translateX(-5%);
            background: none;
            color: $green;
          }
        }
        &:last-child {
          background: none;
          border: 1px solid $green;
          color: $green;
          &:hover {
            transform: translateX(5%);
            background: $green;
            color: black;
          }
        }
        i {
          font-size: 1.3rem;
        }
      }
    }
    .reseaux-accueil {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 30px;
      color: $grey;
      font-size: 1.5rem;
      padding-bottom: 25px;
      opacity: 0;
      animation: fadeIn 0.3s ease-out forwards;
      @keyframes fadeIn {
        from {
          opacity: 0;
        }
        to {
          opacity: 1;
        }
      }
      @media screen and (max-width: 620px) {
        font-size: 1.1rem;
      }
      a {
        text-decoration: none;
        color: inherit;
        i {
          cursor: pointer;
          transition: 0.4s ease-in-out;
          &:hover {
            color: $green;
          }
        }
      }
    }
    .scroll {
      p {
        font-size: 0.8rem;
        opacity: 0;
        animation: fadeIn 0.3s ease-out forwards;
        @keyframes fadeIn {
          from {
            opacity: 0;
          }
          to {
            opacity: 1;
          }
        }
        @media screen and (max-width: 620px) {
          font-size: 0.65rem;
        }
      }
      span {
        display: inline-block; /* nécessaire pour les animations */
        animation: bounce 1s ease-in-out infinite;
        color: $green;
        cursor: default;
        @media screen and (max-width: 620px) {
          font-size: 0.8rem;
        }
      }
      @keyframes bounce {
        0%,
        100% {
          transform: translateY(0); /* position de départ et de fin */
        }
        50% {
          transform: translateY(10px);
        }
      }
    }
  }
}
